.btn-orange-default {
    @apply uppercase inline-block bg-deep-orange text-black;
    text-decoration: none;
    font-family: "SF UI Text Bold", sans-serif;
    transition: 0.2s;
    box-shadow: 5px 5px 2px rgba(25, 26, 29, .34);
    cursor: pointer;
    font-size: 13px;
    line-height: 18px;
    padding: 15px 20px;
    margin: 17px 0 2px 0;
}

.btn-flat {
    box-shadow: none;
}

.btn-white-default {
    @extend .btn-orange-default;
    color: #153e5c;
    background: white;
    border: 1px solid #153e5c;
}

.hover-white-on-blue:hover {
    background: #153e5c;
    color: #fff;
}

.hover-darkgreen:hover {
    background: #379748;
}

.btn-transparent-default {
    @extend .btn-white-default;
    background: transparent;
}

.btn-green-default {
    @extend .btn-white-default;
    background: #52ac62;
    color: white;
}
