h1, h2, h3 {
    letter-spacing: -1px;
    line-height: 1.1;
    font-family: "SF UI Text Bold", sans-serif;
    margin: 20px 0;
    font-weight: normal;
}

h1 {
    font-size: 2em;
}

a {
    font-size: 14px;
    color: #153e5c;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
        text-decoration: none;
    }
}

svg:not(:root).svg-inline--fa {
    display: inline-block;
}

.workshop-default-title {
    letter-spacing: 1.1px;
    font-family: "SF UI Text Regular", sans-serif;
    text-transform: uppercase;
    color: #333;
    font-size: 18px;
    margin: 30px 0 5px 0;

    &::after {
        display: block;
        content: '';
        width: 60px;
        height: 2px;
        background: #333;
        margin-bottom: 20px;
        margin-top: 10px;
    }
}
