.input-type-text {
    @apply appearance-none border py-2 px-3 w-11/12
    text-gray-700 leading-tight;
    border-color: #153e5c;
    border-radius: 3px;

    &:focus {
        box-shadow: 0 0 7px rgb(254, 154, 51);
        border: none;
        outline: none;
    }

    &.required {
        background: #fff url("/images/req.png") 95% center no-repeat;

        &:focus {
            background: none;
        }
    }
}

.form-group-row {
    @apply flex flex-row items-center;
}

.form-default-label {
    @apply block mb-1 text-sm text-gray-800;
}

.form-unit, .form-note, .form-big-unit {
    @apply inline-block w-1/2;
    margin: 10px 0;
    font-family: "SF UI Font Regular", sans-serif;
    font-size: 14px;
}

.form-big-unit {
    @apply inline-block w-full text-gray-800;

    .select-none {
        a {
            outline: none;
            text-decoration: underline;
            color: #153e5c;
            cursor: pointer;
        }
    }
}

.form-note {
    font-size: 12px;
    color: #999;
}

.form-link {
    outline: none;
    text-decoration: underline;
    color: #153e5c;
    cursor: pointer;
}
